*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: black;
  font-weight: 500;
  font-size: 18px;
  width: 100%;
  /* margin-left: 40px; */
  display: flex;
  justify-content: center;
  align-items: center;

}
a:hover{
  color: whitesmoke;
}
.container-fluid{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(145, 153, 197);
  
}
.login-form{
  display: flex;
  flex-direction: column;
  width: 30%;
  /* height: 43%; */
  align-items: center;
  box-shadow: 5px 5px 25px -7px rgba(0,0,0,0.3);
  background-color: whitesmoke;
  letter-spacing: 2px;
  
 

}
.login-form input{
  margin: 10px;
  width: 80%;
}
.login-form a button{
  margin: 10px 0px;
  width: 80%;
  padding: 5px;
  border-radius: 10px;
  font-weight: 500;
  letter-spacing: 2px;
  
  border: 1px solid brown;
}
.login-form button:hover{
  color: whitesmoke;
  background-color: brown;
}
.login-form span{
  font-weight: 500;
}
.register-form{
  width: 30%;
}
form{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 60%;
  align-items: center;
  box-shadow: 5px 5px 25px -7px rgba(0,0,0,0.3);
  background-color: whitesmoke;
  letter-spacing: 2px;
 

}
form h2{
  margin-top: 10px;
}
.register-form input{
  margin: 10px;
  width: 90%;
}
form a button{
  margin: 10px 0px;
  width: 80%;
  padding: 5px;
  border-radius: 10px;
  font-weight: 500;
  letter-spacing: 2px;
 
  border: 1px solid brown;
}
form button:hover{
  color: whitesmoke;
  background-color: brown;
}
form span{
  font-weight: 500; 

}
.logout{
  box-shadow: 5px 5px 25px -7px rgba(0,0,0,0.3);
  background-color: #fff;
  width: 50%;
  height: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.logout h1{
  margin: 20px 10px;
}
.logout button{
  width: 30%;
  justify-content: center;
  padding: 10px;
  margin-top: 20px;
  font-weight: 500;
  border-radius: 10px;
}
.logout button:hover{
  background-color: brown;
  color: white;
}
@media(max-width:899px) {
  
  .register-form{
    width: 50%;
  }
  .login-form{
    width: 50%;
  }
  .logout{
    width: 60%;
    height: 30%;
  }
  
}
@media(max-width:499px){
    
  .register-form{
    width: 70%;
  }
  .login-form{
    width: 70%;
  }
  .logout{
    width: 90%;
    height: 30%;
  }
}